@tailwind base;
@tailwind components;
@tailwind utilities;
@import url("https://fonts.googleapis.com/css2?family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");

* {
  width: auto;
  height: auto;
  margin: 0;
  border: 0;
  padding: 0;
  font-family: "Work Sans", sans-serif;
  font-weight: 400;
  box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  @apply bg-mobile md:bg-desktop bg-contain bg-no-repeat bg-lightpink flex justify-center items-center m-auto;
}

.attribution a {
  @apply text-svgpink underline;
}
